export default{
	// 购物车中选中的数量
	catChoiceCount(state){
		var cartGoods = state.cartGoods;
		var allNum = 0;
		for(var i=0;i<cartGoods.length;i++){
			if (cartGoods[i].isChecked) {
				allNum += cartGoods[i].everyCount;	
			}			
		}
		return allNum;
	},
	// 购物车中的数量
	cartAllCount(state){
		var cartGoods = state.cartGoods;
		var allNum = 0;
		for(var i=0;i<cartGoods.length;i++){			
			allNum += cartGoods[i].everyCount;	
		}
		return allNum;
	},
	checkedAll(state){
		var cartGoods = state.cartGoods;
		var checked = true;
		for(var i=0;i<cartGoods.length;i++){
			if(!cartGoods[i].isChecked){
				checked = false;
				break;
			}
		}
		return checked;
	},
	// 支付界面的价钱
	waitPayPrice(state){
		var allPrice = 0;
		var waitGoods = state.waitGoods;
		for (var i = 0; i < waitGoods.length; i++) {
			allPrice += waitGoods[i].msgPrice*waitGoods[i].everyCount
		}
		return allPrice;
	},
	// 详情页面的价钱
	cartAllPrice(state){
		var cartGoods = state.cartGoods;
		var allPrice = 0;
		for(var i=0;i<cartGoods.length;i++){
			if (cartGoods[i].isChecked) {
				allPrice += cartGoods[i].msgPrice*cartGoods[i].everyCount;
			}
		}
		return allPrice;
		
	},
	// 等待支付的数量
	waitPayCount(state){
		var waitGoods = state.waitGoods;
		var waitNum = 0;
		for (var i = 0; i < waitGoods.length; i++) {
			waitNum += waitGoods[i].everyCount
		}
		return waitNum
	},
	// 等待支付的数量
	payedCount(state){
		var payedGoods = state.payedGoods;
		var waitNum = 0;
		for (var i = 0; i < payedGoods.length; i++) {
			waitNum += payedGoods[i].everyCount
		}
		return waitNum
	},
	// 待支付的价钱
	wiatPayAllPrice(state){
		var waitGoods = state.waitGoods;
		var allPrice = 0;
		for(var i=0;i<waitGoods.length;i++){
			if (waitGoods[i].isChecked) {
				allPrice += waitGoods[i].msgPrice*waitGoods[i].everyCount;
			}
		}
		return allPrice;
		
	}


}


