<template>
  <div class="header">
    <slot></slot>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  }
}
</script>
<style scoped lang="less">
@import url("../../less/base.less");
.header{
  position: fixed;
  height: @navHeight;
  z-index: 1;
  width: 100%;
  h1{
    height: @navHeight;
    line-height: @navHeight;
		background-color: @mallColor;
		.titleStyle(#fff,@titleSize,center)
	}
  .back{
    color:#fff;
    font-size: @iconSize;
    position: absolute;
    top: (@navHeight - @iconSize)/2;
    left: @commonMargin;
  }
}
</style>




