import Vue from 'vue'
import MuseUI from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';
import VueRouter from "vue-router";
import Home from "../components/home/home.vue";
import List from "../components/list/list.vue";
import Cart from "../components/cart/cart.vue";
import Center from "../components/center/center.vue";
import Pay from "../components/pay/pay.vue";
import Payed from "../components/payed/payed.vue";
import Detail from "../components/home/detail/detail.vue";
Vue.use(VueRouter);
Vue.use(MuseUI);
var routes = [
	{path:"/home",component:Home,meta:{"title":"商城首页","keepAlive":true}},
	{path:"/list",component:List,meta:{"title":"商城列表","keepAlive":false}},
	{path:"/cart",component:Cart,meta:{"title":"购物车","keepAlive":false}},
	{path:"/center",component:Center,meta:{"title":"个人中心","keepAlive":false}},
	{
		path:"/detail/:src/:title/:price",
		component:Detail,
		name: '/detail',
		meta:{"title":"商品详情","keepAlive":false}
	},
	{path:"/pay",component:Pay,meta:{"title":"购物车","keepAlive":false}},
	{path:"/payed",component:Payed,meta:{"title":"已购买","keepAlive":false}},
	{path:"*",redirect:"/home"}
];
var router = new VueRouter({
	routes:routes
});

//实现路由前置守卫
router.beforeEach((to,from,next)=>{
	// console.log(to.meta);
	document.title = to.meta.title;
	// 这个next使得路由能够真正切换
	next();
});

export default router;


