export default {
	addAsyn({commit},params){
		commit("add",params)
	},
	isCheckState({commit},params){
		commit('isCheckde',params)
	},
	changeAllCheckState({commit},params){
		commit('changeAllChecked',params)
	},
	longe({commit}){
		commit("longin")
	},
	pushWaitPay({commit}){
		commit("pushWaitPay")
	},
	pushPayed({commit}){
		commit("pushPayed")
	}
}


