<template>
  <div class="cart">
    <div>
      <my-head>
        <h1>购物车</h1>
        <mu-icon class="back"@click="back" value="keyboard_backspace"></mu-icon>
      </my-head>
      <div class="m-top"></div>
      <div v-if="list.length==0">
        <h1>暂无物品</h1>
      </div>
      <div class="msg"
      v-else 
      v-for="(item,index) in list">
        <input type="checkbox" class="xz" 
        :checked="item.isChecked" 
        @click="change($event,index)">
        <div class="img-item">
          <img :src="item.msgSrc" alt="">
        </div>
        <div class="right-item">
          <p class="title-item">{{item.msgTitle}}</p>
          <p class="price-item">￥{{item.msgPrice}}</p>
          <div class="choise">
            <span @click="del(item)">-</span>
            <p>{{item.everyCount}}</p>
            <span @click="item.everyCount++">+</span>
          </div>
        </div>
        <my-cart-bottom></my-cart-bottom>
      </div>
    </div>
  </div>
</template>
<script>
import headTop from "../common/header.vue";
import {mapState,mapActions,mapGetters} from 'vuex';
import carBottom from './cartbottom.vue'
export default {
  data () {
    return {
      
    }
  },
  computed:{
    ...mapState({
      list:'cartGoods'
    })
  },
  methods:{
    back(){
      this.$router.go(-1);
    },
    //勾选事件
    change($event,index){
      this.isCheckState({
        checked:$event.target.checked,
        index:index
      })
    },
    del(item){
      if (item.everyCount<=1) {
        item.everyCount=1
      }else{
        item.everyCount--;
      }
    },
    ...mapActions({
      isCheckState:'isCheckState',
    })
    
  },
  components:{
    "my-head":headTop,
    "my-cart-bottom":carBottom,
    ...mapGetters({
      num:"cartAllCount"
    })
  }
}
</script>
<style scoped lang="less">
.cart{
  .m-top{
    height: 44px;
  }
  .msg{
    .xz{
      margin: 20px 10px;

      width: 40px;
      height: 40px;
    }
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    .img-item{
      margin-right: 10px;
      height: 100px;
      img{
        width: 90px;
        height: 90px;
      }
    }
    .right-item{
      .title-item{
        font-weight: 700;
        font-size: 14px;
        color: #333;
      }
      .price-item{
        line-height: 16px;
        color: #e4393c;
        font-size: 16px;
      }
      .choise{
        display: inline-block;
        display: flex;
        flex-direction: row;
        align-items: center;
        position: absolute;
        bottom: 0;
        right: 20px;
        span{
          font-size: 30px;
          font-weight: bold;
          margin: 0 10px;
        }
      }
    }
  }
  .no-shopping{

    .aa{
      position: absolute;
      top: 100px;
      left: 0;
      width: 100%;
      font-size: 40px;
      text-align: center;
    }
  }
}
  
</style>




