<template>
	<div class="add-cart">
		<div class="seller-cart">
			<div class="seller">
				<mu-icon value="person_pin"></mu-icon>
				<p>卖家</p>
			</div>
			<div class="enter-shop">
				<mu-icon value="list"></mu-icon>
				<p>进店</p>
			</div>
			<div class="cart" @click="enterCart">
				<mu-icon value="shopping_cart"></mu-icon>
				<p>购物车</p>
				<!-- 显示购买数量 -->
				<span class="count" 
				:style="{width:$store.state.count<10?'12px':'14px'}">
					{{buyCount}}
				</span>
			</div>
		</div>
		<div class="buy">
			<p class="now-add" @click="myadd">加入购物车</p>
			<p class="now-buy">立即购买</p>
		</div>
	</div>
</template>
<script>
	import {mapState,mapActions,mapGetters} from 'vuex'
	export default{
		data(){
			return {
				
			};
		},
		computed:{
			...mapGetters({
				buyCount:'cartAllCount'
			})
		},
		methods:{
			...mapActions({
				addAsyn:'addAsyn',
			}),
			enterCart(){
				this.$router.push("/cart")
			},
			myadd(){
				// this.addAsyn({money:this.msgPrice,title:this.msgTitle,pic:this.msgSrc}),
				this.addAsyn({
					everyCount:this.num,
					msgSrc:this.msgSrc,
					msgTitle:this.msgTitle,
					msgPrice:this.msgPrice,
					isChecked:true
				})
				this.$emit("gai");
			},
			...mapGetters({
				
			})
		},
		props:['msgPrice','msgTitle','msgSrc','num']
	}
</script>
<style scopde lang="less">
	@import url("../../../less/base.less");
	.add-cart{
		width: 100%;
		.flexRowCenter();
		position: fixed;
		left: 0;
		bottom: 0;
		.seller-cart{
			.flexRowCenter();
			flex:1;
			justify-content: space-around;
			.cart{
				position: relative;
				text-align: center;
				.count{
					font-size: 8px;
					height: 12px;
					border-radius: 50%;
					background-color: red;
					color:white;
					position: absolute;
					top: -3px;
					right: 5px;
				}
			}
		}
		.buy{
			.flexRowCenter();
			flex:1;
			.now-add{
				flex:1;
				line-height: 50px;
			    text-align: center;
			    background: #ff9600;
			    color: #fff;
			    font-size: 14px;
			    cursor: pointer;
			
			};
			.now-buy{
				flex:1;
			    line-height: 50px;
			    text-align: center;
			    background: #e4393c;
			    color: #fff;
			    font-size: 14px;
			    cursor: pointer;
			}
		}
	}
</style>


