<template>
	<div class="longined">
		<div class="header">
			<img :src="imgSrc" alt="">
			<p>用户名</p>
		</div>
		<div class="main">
			<div class="icon-item" @click="enterPay">
				<mu-icon value="person_pin" color="red"></mu-icon>
				<p>待付款</p>
				<!--  -->
				<p class="count" 
				:style="{width:waitCount<10?'12px':'14px'}"
				v-if="waitCount > 0">
					{{waitCount}}
				</p>
			</div>
			<div class="icon-item" @click="enterCart">
				<mu-icon value="shopping_cart" color="orange"></mu-icon>
				<p>购物车</p>
				<p class="count" 
				:style="{width:cartCount<10?'12px':'14px'}"
				v-if="cartCount > 0">
					{{cartCount}}
				</p>
			</div>
			<div class="icon-item" @click="enterPayed">
				<mu-icon value="list" color="green"></mu-icon>
				<p>已购买</p>
				<p class="count" 
				:style="{width:payedCount<10?'12px':'14px'}"
				v-if="payedCount > 0">
					{{payedCount}}
				</p>
			</div>
		</div>
	</div>
</template>
<script>
	import headTop from "../common/header.vue";
	import {mapGetters} from "vuex";
	export default{
		data(){
			return{
				imgSrc:"https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2988916786,258747399&fm=27&gp=0.jpg"
			}
		},
		methods:{
			enterCart(){
				this.$router.push("/cart")
			},
			enterPay(){
				this.$router.push("/pay")
			},
			enterPayed(){
				this.$router.push("/payed")
			}
		},
		computed:{
			...mapGetters({
				waitCount:"waitPayCount",
				payedCount:"payedCount",
				cartCount:"cartAllCount"
			})
		}
	}
</script>
<style scopde lang="less">
	@import url("../../less/base.less");
	.longined{
	    .header{
	    	background-color: #CAC4C4;
	    	padding: 20px;
	    	display: flex;
	    	.flexColumnCenter();
    		img{
				width: 100px;
				height: 100px;
				border-radius: 50%;
			}
			P{
				margin-top: 10px;
			}
	    }
	    .main{
	    	padding: 20px 0;
			.flexRowCenter();
			justify-content: space-around;
			.icon-item{
				position: relative;
				.flexColumnCenter();
				.count{
					height: 12px;
					border-radius: 50%;
					background-color: red;
					font-size: 8px;
					color:white;
					position: absolute;
					top: -3px;
					right: 5px;
					text-align: center;
				}
			}
	    }
  	}
</style>


