<template>
	<div class="recommend">
		<h3 class="recommend-top">为你推荐</h3>
			<router-link to="/center" class="recommend-login">登录账号，查看专属推荐商品 &gt</router-link>
		<div class="recommend-item" @click="enterDetail(item.imgSrc,item.title,item.price)" v-for="item in arr">
			<div class="recommend-img" >
				<img :src="item.imgSrc" alt="">
			</div>
			<p class="recommend-title">{{item.title}}</p>
			<p class="recommend-price">￥{{item.price}}</p>
		</div>
	</div>
</template>
<script>
	// 引入api.js这个文件即可
	import {recommand} from "../api.js";
	export default{
		data(){
			return{
				arr:[]
			}
		},
		created(){
			this.getCarousel();
		},
		methods:{
			getCarousel(){
				recommand().then(result=>this.arr=result)
			},
			enterDetail(src,title,price){
				this.$router.push({name:'/detail',params:{src:src,title:title,price:price}})
			}
		}
	}
</script>

<style lang="less" scoped>
	@import url("../../less/base.less");
	@mTop:10px;
	.recommend{
		.recommend-top{
			padding:@mTop;
			color:#838588;
			text-align: center;
			width: 100%;
		}
		.recommend-login{
			text-align: center;
			width: 100%;
			color: #f23030;
		    font-size: .875rem;
		    line-height: 1.5;
		    text-align: center;
		    padding: 3px 3px 14px;

		}
		.flexRowCenter();
		flex-wrap: wrap;
		.recommend-item{
			width: 48%;
			img{
				width: 100%;
				height: 200px;
			}
			.recommend-img{
				img{
					width: 100%;
				}
			}
			.recommend-title{
			    height: 33px;
			    font-size: 13px;
			    overflow: hidden;
			    text-overflow: ellipsis;
			    display: -webkit-box;
			    word-break: break-word;
			    color: #232326;
			    margin-top: 5px;
			    line-height: 17px;
			    margin-bottom: 3px;
			    padding: 0 4px;
			}
			.recommend-price{
				font-size: 16px;
				color: #f23030;
				margin: @mTop 0;
			}
		}
	}
	
</style>


