<template>
	<div class="detail">
		<my-head>
			<h1>详情页面</h1>
			<mu-icon class="back" @click="back" value="keyboard_backspace"></mu-icon>
		</my-head>
		<div class="content">
			<img :src="iconSrc" alt="">
			<p class="title">{{title}}</p>
			<p class="price">￥{{price}}</p>
			<div class="choise">
	            <span @click="del">-</span>
	            <input type="text" v-model="everyCount">
	            <span @click="everyCount++">+</span>
	        </div>
		</div>
		<mu-container>
			<mu-dialog width="360" :open.sync="openSimple">
				<div id="popup">
		   			<h1 class="addSuccess">加入购物车成功!</h1>
				</div>
			</mu-dialog>
		</mu-container>


		<my-add-cart @gai="open" :num="everyCount" :msgPrice="price" :msgTitle="title" :msgSrc="iconSrc"></my-add-cart>
	</div>
</template>

<script>
	import headTop from "../../common/header.vue";
	import addCart from "./addcart.vue";

	export default{
		data(){
			return {
				iconSrc:"",
				title:"",
				price:"",
				everyCount:1,
				openSimple: false
			};
		},
		methods:{
		    back(){
		      this.$router.go(-1);
		    },
		    del(){
		      if(this.everyCount <= 1){
		        this.everyCount = 1
		      }else{
		        this.everyCount--;
		      }
		    },
		    open(){
		    	this.openSimple = true;
		    	var that = this;
		    	setTimeout(function(){
		    		that.openSimple = false;
		    	},700)
		    }
		},
		components:{
		    "my-head":headTop,
		    "my-add-cart":addCart,
		},
		created(){
			this.iconSrc = this.$route.params.src;
			this.title = this.$route.params.title;
			this.price = this.$route.params.price;
		}
	}
</script>
<style scoped lang="less">

	.detail{
		.content{
			width: 100%;
			img{
				width: 100%;
				height: 400px;
			}
			.title{
			    color: #333;
			    font-size: 16px;
			    font-weight: 400;
			    line-height: 18px;
			}
			.price{
				font-size: 18px;
			    color: #e4393c;
			    font-weight: 700;
			    line-height: 1.3;
			    display: inline-block;
			}
			.choise{
		        display: inline-block;
		        position: absolute;
		        right: 20px;
		        input{
		          width: 20px;
		        }
		    }
		}
		.mu-dialog-scrollable .mu-dialog-body{
			color: red;
		}
		
		
	}

</style>
<style>
	p{
		flex-direction: column;
	}
</style>


