<template>
  <div class="cart">
    <div>
    	<my-head>
	        <h1>已购买</h1>
	        <mu-icon class="back"@click="back" value="keyboard_backspace"></mu-icon>
    	</my-head>
    	<div class="m-top"></div>
    	<div v-if="list.length==0">
    		<h1>暂无物品</h1>
    	</div>
    	<div v-else>
    		<h1>东西配送中。。。</h1>
	    	<div class="msg" 
	    	v-for="(item,index) in list">
		        <div class="img-item">
		        	<img :src="item.msgSrc" alt="">
		        </div>
		        <div class="right-item">
		        	<p class="title-item">{{item.msgTitle}}</p>
		        	<p class="price-item">￥{{item.msgPrice}}</p>
		        	<div class="choise">
		            	<p>{{item.everyCount}} 件</p>
		        	</div>
		        </div>
	    	</div>
    	</div>
    	
    </div>
  </div>
</template>
<script>
import headTop from "../common/header.vue";
import {mapState,mapActions,mapGetters} from 'vuex';
export default {
  data () {
    return {
      
    }
  },
  computed:{
    ...mapState({
      list:'payedGoods'
    })
  },
  methods:{
    back(){
      this.$router.go(-1);
    },
    //勾选事件
    change($event,index){
      this.isCheckState({
        checked:$event.target.checked,
        index:index
      })
    },
    ...mapActions({
      
    })
    
  },
  components:{
    "my-head":headTop,
    ...mapGetters({
    	
    })
  }
}
</script>
<style scoped lang="less">
.cart{
	.m-top{
		height: 44px;
	}
  .msg{
    .xz{
      margin: 20px 10px;

      width: 40px;
      height: 40px;
    }
    position: relative;
    top: 44px;
    left: 0;
    display: flex;
    flex-direction: row;
    .img-item{
      margin-right: 10px;
      height: 100px;
      img{
        width: 90px;
        height: 90px;
      }
    }
    .right-item{
      .title-item{
        font-weight: 700;
        font-size: 14px;
        color: #333;
      }
      .price-item{
        line-height: 16px;
        color: #e4393c;
        font-size: 16px;
      }
      .choise{
        display: inline-block;
        display: flex;
        flex-direction: row;
        align-items: center;
        position: absolute;
        bottom: 0;
        right: 20px;
        span{
          font-size: 30px;
          font-weight: bold;
          margin: 0 10px;
        }
      }
    }
  }
  .no-shopping{

    .aa{
      position: absolute;
      top: 100px;
      left: 0;
      width: 100%;
      font-size: 40px;
      text-align: center;
    }
  }
}
  
</style>




