<template>
	<mu-carousel>
     	<mu-carousel-item v-for="item in arr">
       		<img :src="item.imgSrc" alt="">
     	</mu-carousel-item>
    </mu-carousel>

</template>
<script>
	import {carousels} from "../api.js";
	export default{
		data(){
			return{
				arr:[]
			}
		},
		created(){
			this.getCarousel();
		},
		methods:{
			getCarousel(){
					carousels().then(result=>this.arr=result
					)
				 	
			}
		}
	}
</script>
<style lang="less" scoped>
	@import url("../../less/base.less");
	.mu-carousel{
		position: relative;
		top: @navHeight;
     	height: 200px;
     	.mu-carousel-item{
	      	width: 100%;
	        height: 200px;
	        img{
	        	width: 100%;
	        	height: 200px;
	        }
	    }
    }

</style>


