import Vue from 'vue';
import Vuex from 'vuex';
import state from './state.js';
import actions from './action.js';
import mutations from './mutation.js';
import getters from './getter.js';
Vue.use(Vuex);

var store = new Vuex.Store({
	state:state,
	actions:actions,
	getters:getters,
	mutations:mutations,
	namespaced: true
})
	

export default store


