<template>
  <div class="tabbar">
    <mu-bottom-nav v-if="isShowTabbar">
      <mu-bottom-nav-item title="首页" icon="home" to="/home">
      </mu-bottom-nav-item>
      <mu-bottom-nav-item title="列表" icon="list" to="/list">
      </mu-bottom-nav-item>
      <mu-bottom-nav-item title="购物车" icon="shopping_cart" to="/cart">
      </mu-bottom-nav-item>
      <mu-bottom-nav-item title="个人中心" icon="location_on" to="/center">
      </mu-bottom-nav-item>
    </mu-bottom-nav>
  </div>
</template>
<script>
export default {
  data () {
    return {
      isShowTabbar : true
    }
  },
  watch:{
    // 路径
    $route(to,from){
      var path = to.path;
      if (path == "/home" || path == "/list" || path == "/find") {
        this.isShowTabbar = true;
      }else{
        this.isShowTabbar = false;
      }
    }
  }
}
</script>
<style scoped>
.tabbar{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 1;
}
.tab-item{
	width: 100%;
	height: 100%;
}
a{
  color:#909399;
}
.router-link-exact-active{
  color: #f23030;
}
.count{
  font-size: 8px;
  height: 12px;
  border-radius: 50%;
  background-color: red;
  color:white;
  position: absolute;
  top: -3px;
  right: 5px;
}
.number{
  position: absolute;
  top: 5px;
  right: 23%;
}
.count{
  font-size: 8px;
  height: 12px;
  border-radius: 50%;
  background-color: red;
  color:white;
  position: absolute;
  top: -3px;
  right: 5px;
}

</style>


