<template>
	<div class="pay">
		<my-head>
	    	<h1>支付页面</h1>
	    	<mu-icon class="back" @click="back" value="keyboard_backspace"></mu-icon>
	    </my-head>
	    <div class="m-top"></div>
		<div class="msg" v-if="allPrice!=0">
			<h2>需要支付：</h2>
			<h1>{{allPrice}}元</h1>
			<mu-button color="primary" @click="pay">支付</mu-button>	
		</div>
		<div class="msg" v-if="allPrice==0">
			<p>支付成功！</p>
			<p>还有<span>{{sec}}</span>秒返回</p>
		</div>
	</div>
</template>
<script>
	import headTop from "../common/header.vue";	
	import {mapGetters,mapActions} from "vuex";
	export default{
		data(){
			return{
				sec:3
			}
		},
		computed:{
			...mapGetters({
		  		allPrice:"waitPayPrice"
		    })
		},
		components:{
		    "my-head":headTop,
		},
		methods:{
			back(){
				this.$router.go(-1)
			},
			pay(){
				var timer = setInterval(()=>{
					if (this.sec<=0) {
						this.$router.replace("/home");
						clearInterval(timer);
					}else{
						this.sec--;
					}
				},1000);
				this.pushPayed();
			},
			...mapActions({
				pushPayed:"pushPayed"
			})
		}
	}
</script>
<style scopde lang="less">
 	@import url("../../less/base.less");
	.pay{
		.m-top{
	    	height: @navHeight;
	    }
	    .msg{
	    	padding: 20px;
	    	width: 200px;
	    	height: 200px;
	    	border:1px solid gray;
	    	margin:100px auto;
	    	.flexColumnCenter();
	    	justify-content: space-around;
	    	h1{
	    		font-size: 20px;
	    		color:@mallColor;
	    	}
	    	span{
	    		font-size: 30px;
	    		color:@mallColor;
	    	}

	    }
	}
</style>


