export default {
	addCount(state,obj){
		state.count++;
		state.priceCount.push(obj.money);
		state.srcCount.push(obj.pic);
		state.titleCount.push(obj.title);
		state.allPrice += Number(obj.money);
	},
	isCheckde(state,obj){
		var checked = obj.checked;
		var index = obj.index;
		state.cartGoods[index].isChecked=checked;
	},
	add(state,obj){
		//判断是否有相同数据用
		var isExit = false;
		var cartGoods = state.cartGoods;
		for(var i=0;i<cartGoods.length;i++){
			if(cartGoods[i].msgSrc == obj.msgSrc){
				isExit = true;
				cartGoods[i].everyCount += obj.everyCount;
				break;
			};
		}	
		if(!isExit){
			state.cartGoods.push(obj);
		}
	},
	changeAllChecked(state,obj){
		// 获取到对应的勾选状态
		var checked = obj.checked;
		// 根据勾选状态去影响其他列表勾选
		for(var i=0;i<state.cartGoods.length;i++){
			state.cartGoods[i].isChecked = checked;
		}
	},
	longin(state){
		state.isLongin = true;
	},
	pushWaitPay(state){
		var cartGoods = state.cartGoods;
		var waitGoods = state.waitGoods;
		for(var i=0;i<cartGoods.length;i++){
			if (cartGoods[i].isChecked) {
				cartGoods[i].isChecked = false;
				waitGoods.push(cartGoods[i]);
				cartGoods.splice(i,1);
				i--;
			}
		}
	},
	pushPayed(state){
		var waitGoods = state.waitGoods;
		var payedGoods = state.payedGoods;
		for (var i = 0; i < waitGoods.length; i++) {
			payedGoods.push(waitGoods[i])
			waitGoods.splice(i,1);
			i--;
		}
	},
	changeWaitAllChecked(state,obj){
		// 获取到对应的勾选状态
		var checked = obj.checked;
		// 根据勾选状态去影响其他列表勾选
		for(var i=0;i<state.cartGoods.length;i++){
			state.cartGoods[i].isChecked = checked;
		}
	}
}


