<template>
	<div class="classify">
		<div class="classify-item" v-for="item in arr"> 
			<img :src="item.imgSrc" alt="">
			<p>{{item.title}}</p>
		</div>
	</div>
</template>
<script>
	// 引入api.js这个文件即可
	import {classify} from "../api.js";
	export default{
		data(){
			return{
				arr:[]
			}
		},
		created(){
			this.getCarousel();
		},
		methods:{
			getCarousel(){
					classify().then(result=>this.arr=result)
			}
		}
	}
</script>
<style lang="less" scoped>
	@import url("../../less/base.less");
	@mTop:10px;
	.classify{
		margin-top: @navHeight;
		border-radius: 10px;
		.flexRowCenter();
		flex-wrap: wrap;
		.classify-item{
			width: 20%;
			text-align: center;
			img{
				margin-top: @mTop;
				width: 65%;
			}
			p{
				margin-top: @mTop;
			}
		}
	}
	
</style>


