<template>
  <div class="center">
  	<my-head>
      <h1>个人中心</h1>
      <mu-icon class="back" @click="back" value="keyboard_backspace"></mu-icon>
    </my-head>
    <div class="m-top"></div>
    <longined v-if="isLongin"></longined>
    <un-longined v-if="!isLongin"></un-longined>
  </div>

</template>

<script>
import headTop from "../common/header.vue";	
import longined from "./longined.vue";
import unLonginde from "./unlonginde.vue";
import {mapState,mapActions} from "vuex";
export default {
  data () {
    return {
    }
  },
  methods:{
    back(){
      this.$router.go(-1);
    }
  },
  components:{
    "my-head":headTop,
    "longined":longined,
    "un-longined":unLonginde
  },
  computed:{
     ...mapState({
      "isLongin":"isLongin"
    })
  }
}
</script>
<style scoped lang="less"> 
  .center{
    .m-top{
      height: 44px;
    }
  }

</style>




