<template>
	<div class="un-longin">
		<mu-text-field icon="person_pin" 
		placeholder="电话号码" 
		v-model="username">
		</mu-text-field>

		<mu-text-field icon="comment" 
		placeholder="请输入密码" 
		v-model="pwd">
		</mu-text-field>
		<mu-button color="primary" @click="longin">登录</mu-button>	

	</div>
</template>
<script>
	import {mapState,mapActions} from "vuex";
	export default {
		data(){
			return{
				username:"",
				pwd:""
			}
			
		},
		methods:{
			...mapActions({
				longe:"longe"
			}),
			longin(){
				if (this.username == "" || this.pwd == "") {
					console.log("请输入正确的账号or密码！！！")
				}else{
					this.longe()
				}
			}
		}
	}
</script>
<style scopde lang="less">
	.un-longin{
		.mu-input{
			margin-top: 44px;
			width: 100%;
			.mu-text-field{
				input{
					width: 100%;
				}
			}
		}
		.mu-button{
			width: 100%;
			.mu-button-wrapper{
				width: 100%;
			}
		}
		
	}
</style>


