<template>
	<div class="cart-bottom">
		<div class="left">
			<input type="checkbox" 
			:checked="checkedAll" 
			@change="changeAll($event)"
			class="choice">
			<p>全选</p>
		</div>
		<div class="right">	
			<span>总计:</span>		
			<p class="all-price">￥{{cartAllPrice}}</p>
			<p class="settlement"
			@click="enterPay">
			去结算({{catChoiceCount}})件
			</p>
		</div>
	</div>
</template>
<script>
	import {mapGetters,mapActions,mapState} from 'vuex';
	export default{
		computed:{
			...mapGetters({
				cartAllPrice:"cartAllPrice",
				catChoiceCount:"catChoiceCount",
				checkedAll:"checkedAll"
			}),
			...mapState({
		    	isLongin:'isLongin'
		    })
		},
		methods:{
			changeAll($event){
			    this.changeAllCheckState({
			        checked:$event.target.checked
			    })
     		},
     		...mapActions({
     			changeAllCheckState:"changeAllCheckState",
     			pushWaitPay:"pushWaitPay"
     		}),
     		enterPay(){
     			if(this.isLongin){
     				this.$router.push("/pay");
     				this.pushWaitPay()
     			}else{
     				this.$router.push("/center")
     			}
     		}
		}
		

	}
</script>
<style scoped lang="less">
	@import url("../../less/base.less");
	.cart-bottom{
		width: 100%;
		height: 64px;
		background-color: #fff;
		position: absolute;
		bottom: 0;
		left: 0;
		padding: 10px;
		display: flex;
		justify-content: space-between;
		.choice{
			width: 15px;
			height: 15px;
			margin-left: 5px;
		}
		.left{
		    width: 40px;
		    font-size: 10px;
		    text-align: center;
		    height: 15px;
		    color: #999;
		}
		.right{
			display: flex;
			justify-content: space-between;
			span,.all-price{
			    padding-top: 10px;
			    font-weight: 700;
			    line-height: 1em;
			    height: 50px;
			    text-align: right;
			    float: left;
			    font-size: 16px;
			}
			.all-price{
			    color: #e4393c;
			}
			.settlement{
			    float: right;
			    display: block;
			    width: 110px;
			    height: 50px;
			    margin-left: 10px;
			    line-height: 50px;
			    font-size: 16px;
			    text-align: center;
			    font-weight: 700;
		        background: #e4393c;
    			color: #fff;
			}
		}
	}
</style>


