<template>
  <div class="list">
  	<my-head><h1>列表</h1></my-head>
    <mu-list class="left-list">
      <template v-for="(item,index) in arr" >
        <mu-list-item class="category-item">
          <mu-list-item-title 
          @click="change(index)"
          :style='{color:(index==currentIndex?"red":"black")}'>
            {{item.title}}
          </mu-list-item-title>
        </mu-list-item>
        <mu-divider />
      </template>
    </mu-list> 
    <div class="list-right" >
      <h1>6666666</h1>
    </div>
  </div>
</template>

<script>
import headTop from "../common/header.vue";
import {listGoods} from "../api.js"
export default {
  data () {
    return {
      arr:[],
      currentIndex:0
    }
  },
  components:{
    "my-head":headTop
  },
  created(){
    this.getDate()
  },
  methods:{
    getDate(){
      listGoods().then((res)=>{
      this.arr = res.all;
     });
    },
    change(index){
      this.currentIndex = index;
    }
  }
}
</script>
<style scoped lang="less">
@import url("../../less/base.less");
  .list{
    width: 100%;
    overflow: hidden;
    position: absolute;
    .left-list{
      margin-top:@navHeight;
      position: fixed;
      top: 0;
      left: 0;
      background-color:#F8F8F8;
      width:@navHeight * 3;
      height:100%;
      .category-item{
        width:100%;
        height:@navHeight;
        text-align:center;
        line-height:@navHeight;
      }
    }
    .list-right{
      position: relative;
      top: 0;right: 0;
      width: 100%;
      height: 1000px;
      /*background-color: yellow;*/
      margin-top:@navHeight;
      margin-left: @navHeight * 3;
      
    }
  }
</style>




