// 引入请求所需的axios框架
import axios from 'axios';
// 对于axios中baseUrl属性的设置
// http://pbdk4j5sg.bkt.clouddn.com
axios.defaults.baseURL = 'http://pbkt7f9eb.bkt.clouddn.com';
var carousels = ()=>{
	return axios.get("/home_carousel.json").then(response=>{
		return response.data;
	});
}
// 请求分类所需的方法
var classify = ()=>{
	return axios.get("/home_classifys.json").then(response=>{
		return response.data;
	});
}
// 请求商品推荐所需的方法
var recommand = ()=>{
	return axios.get("/mall_recommended.json").then(response=>{
		return response.data;
	});
}
// list列表
var listGoods = ()=>{
	return axios.get("http://pbdk4j5sg.bkt.clouddn.com/list_category.json").then(response=>{
		return response.data;
	});
}
export{carousels,classify,recommand,listGoods}


